<template>
	<div class="green-certification-19" v-loading="loading" element-loading-spinner="iconfont icon-loading1" element-loading-background="rgba(0, 0, 0, 0.2)">
		<area-panel area-title="绿色认证" @hand-sub-title="handlePage">
			<div v-show="!loading" class="panel-wrapper" slot="panelMain">
				<div class="end-time">
					<span>数据截止日期：2019年12月</span>
				</div>
				<div class="top-box">
					<div class="left"></div>
					<div class="center">
						<p>绿色认证率</p>
						<p>{{ dataObj.greenCertifiRate || 0 }}<span>%</span></p>
					</div>
					<div class="right"></div>
				</div>
				<div class="bottom-box">
					<div class="left">
						<p>绿色认证面积</p>
						<p>{{ dataObj.greenCertifiArea || 0 }}<span>万亩</span></p>
						<p v-show="dataObj.greenAreaCompareLastYearRate !== 0">
							同比：<span>{{ dataObj.greenAreaCompareLastYearRate || 0 }}% <i class="iconfont icon-up"></i></span>
						</p>
					</div>
					<div class="right">
						<p>绿色认证企业</p>
						<p>{{ dataObj.greenCertifiCompany || 0 }}<span>家</span></p>
						<p v-show="dataObj.greenCompanyCompareLastYear !== 0">
							同比：<span>{{ dataObj.greenCompanyCompareLastYear || 0 }}家 <i class="iconfont icon-up"></i></span>
						</p>
					</div>
				</div>
			</div>
		</area-panel>
	</div>
</template>
<script>
import areaPanel from '@/components/overview/area-panel';
import {areaLevelCorAreaId} from '@/common/fn';
export default {
	props: {
		farmAreaInfo: {
			type: Object,
			default() {
				return areaLevelCorAreaId();
			}
		}
	},
	data() {
		return {
			dataObj: {},
			loading: true
		};
	},
	mounted() {
		this.initMoudle();
	},
	methods: {
		async initMoudle() {
			this.getGreenCertifiByArea();
		},
		getGreenCertifiByArea() {
			this.$ajax
				.post(this.$api.safeSupervise.getGreenCertifiByArea, {
					farmAreaId: [this.farmAreaInfo.farmAreaId],
					farmAreaType: this.farmAreaInfo.farmAreaType
				})
				.then(({data}) => {
					if (data.respCode === 0) {
						this.dataObj = data.obj;
						this.loading = false;
					}
				})
				.catch(e => {
					console.log('绿色认证 error: ', e);
				});
		},
		handlePage() {
			// this.$router.push({
			// 	name: 'greenCertList'
			// });
			this.$emit('handleClick', 'greenCertList', '绿色认证');
		}
	},
	components: {areaPanel}
};
</script>

<style lang="scss" scoped>
.green-certification-19 {
	width: 100%;
	height: 100%;
	.panel-wrapper {
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;

		.end-time {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			color: rgba(0, 246, 255, 0.5);
			font-size: 14px;
		}

		.top-box {
			width: 73%;
			height: 32.1%;
			min-height: 95px;
			display: flex;
			margin: 5% auto 0;
			.left {
				width: 30px;
				height: 100%;
				background: url('../../../assets/images/overview/green-certification-left.png') no-repeat;
				background-size: 100% 100%;
			}
			.right {
				width: 30px;
				height: 100%;
				background: url('../../../assets/images/overview/green-certification-right.png') no-repeat;
				background-size: 100% 100%;
			}
			.center {
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				flex-grow: 1;
				height: 100%;
				background: url('../../../assets/images/overview/green-certification-center.png');
				background-size: 100% 100%;
				p:nth-child(1) {
					font-size: 18px;
					font-weight: bold;
					color: #9dd1d7;
					margin-bottom: 5%;
				}
				p:nth-child(2) {
					font-size: 30px;
					font-weight: bold;
					color: #feeb48;
					span {
						font-size: 24px;
						font-weight: 400;
					}
				}
			}
		}
		.bottom-box {
			display: flex;
			justify-content: space-between;
			width: 69%;
			margin: 8% auto 0;
			> div {
				display: flex;
				flex-direction: column;
				align-items: center;
				p:nth-child(1) {
					font-size: 14px;
					font-weight: 400;
					color: #9dd1d7;
				}
				p:nth-child(2) {
					font-size: 24px;
					font-weight: 400;
					color: #00f3fd;
					margin: 8% 0;
					span {
						margin-left: 10px;
						font-size: 16px;
					}
				}
				p:nth-child(3) {
					font-size: 12px;
					font-weight: 400;
					color: #71ccde;
					span {
						color: #76ff03;
						.iconfont {
							font-size: 12px;
						}
					}
				}
			}
		}
	}
	/deep/ .el-loading-spinner {
		overflow: hidden;
		.iconfont {
			display: block;
			font-size: 40px;
			color: #00f6ff;
			animation: loading 1.25s linear infinite;
			margin-top: 24px;
			margin-left: 8px;
		}
	}
}
@media screen and (min-width: 3000px) {
	.green-certification-19 {
		.panel-wrapper {
			.end-time {
				font-size: 16px;
			}

			.top-box {
				margin: 2% auto 0;
				height: 40%;
				.center {
					p:nth-child(1) {
						font-size: 26px;
					}
					p:nth-child(2) {
						font-size: 43px;
						span {
							font-size: 36px;
						}
					}
				}
			}
			.bottom-box {
				> div {
					p:nth-child(1) {
						font-size: 26px;
					}
					p:nth-child(2) {
						font-size: 36px;
						span {
							font-size: 24px;
						}
					}
					p:nth-child(3) {
						font-size: 18px;
						span {
							color: #76ff03;
							.iconfont {
								font-size: 18px;
							}
						}
					}
				}
			}
		}
	}
	.green-certification-19 .panel-wrapper .bottom-box {
		margin: 6% auto 0;
	}
}
</style>



